<template>
  <div id="language">
    <div class="top" >
      <img src="../../images/language_left.png"  @click="goback"> 
      <span>{{$t("message.languageSetting.languageSetting")}}</span>
    </div>
    <ul style="width:100%">
      <li v-for="(item,index) in languageList" :key="index" @click="changeLanguage(item)">
        <div class="languageList">
          <span>{{item.language}}</span>
          <i :class="language === item.label?`${item.icon}click`:`${item.icon}default`"  class="icon"></i>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      language: "zh",
      languageList: [
        {
          icon: "language-icon-",
          language: "简体中文",
          label: "zh"
        },
        {
          icon: "language-icon-",
          language: "English",
          label: "en"
        }
      ]
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log(vm.$i18n.locale);
      vm.language = vm.$i18n.locale;
    });
  },
  computed: {
    languages() {
      if (this.language === "zh") {
        return "language-icon-default";
      } else {
        return "language-icon-click";
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    changeLanguage(item) {
      this.language = item.label;
      this.$i18n.locale = item.label;
      localStorage.setItem("languageType", this.$i18n.locale);
    }
  }
};
</script>

<style scoped lang="scss">
.top {
  width: 100%;
  height: 0.88rem;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.28rem;
  color: #fff;
  background-color: #005c9c;
  img {
    width: 0.24rem;
    height: 0.36rem;
    position: absolute;
    left: 0.2rem;
    top: 0.24rem;
  }
}
.languageList {
  height: 0.8rem;
  width: 90%;
  padding: 0 0.3rem;
  border-bottom: 1px solid #e9e9ea;
  line-height: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.icon {
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 0.26rem;
  height: 0.26rem;
}
</style>



